<template>
  <a-form class="info-form" layout="inline" :form="form">
    <a-form-item
      v-for="(v,i) in list"
      :key="i"
      :label="v.text"
      :label-col="labelCol"
      :wrapper-col="wrapperCol"
    >
      <a-select v-if="v.dom==='select'" v-decorator="[v.key]">
        <a-select-option v-for="(v,i) in shopList" :value="v.label" :key="i">{{v.value}}</a-select-option>
      </a-select>
      <a-input
        v-else
        :disabled="v.key==='code'"
        v-decorator="[v.key,{rules:[{required:v.required||false, message: '请输入'+v.text }]}]"
      />
    </a-form-item>
  </a-form>
</template>
<script>
export default {
  props: {
    form: {},
    list: {
      default: v => {
        return v;
      }
    },
    labelCol: {
      type: Object,
      default: () => {
        return {
          span: 10
        };
      }
    },
    wrapperCol: {
      type: Object,
      default: () => {
        return {
          span: 14
        };
      }
    }
  },
  mounted(){
    console.log(this.list)
  }
};
</script>